<template>
  <div class="setting-tab">
    <b-tabs vertical nav-wrapper-class="nav-vertical">
      <b-tab active>
        <template #title>
          <feather-icon icon="LayersIcon"/> Group Management
        </template>
        <div class="ml-3">
          <!-- Platform Groups Table -->
          <div class="mb-5">
            <h4 class="mb-1">Platform Group</h4>

            <div class="rounded my-shadow">
              <validation-observer ref="platformGroup">
                <b-table-simple no-border-collapse sticky-header bordered responsive striped  class="rounded mb-0 setting-risk-threshold-table">
                  <b-thead>
                    <b-tr>
                      <b-th>Group Name</b-th>
                      <b-th>Parent Group</b-th>
                      <b-th>Server Name</b-th>
                      <b-th>Platform Group</b-th>
                      <b-th>Currency</b-th>
                      <b-th>Date Created</b-th>
                      <b-th/>
                    </b-tr>
                  </b-thead>
                  <b-tbody>
                    <b-tr v-for="(item, index) in platform_groups" :key="index">
                      
                      <b-td class="text-left">{{item.settings.name}}</b-td>
                      <b-td class="">{{item.parent_group}}</b-td>
                      <b-td class="">{{item.settings.server_name}}</b-td>
                      <b-td class="">{{item.settings.platform_group}}</b-td>
                      <b-td class="">{{item.settings.currency}}</b-td>
                      <b-td class="">{{item.settings.created}}</b-td>
                      <b-td class=""><feather-icon icon="EditIcon" size="14" class="text-success cursor-pointer"/></b-td>
                    </b-tr>

                    <b-tr v-if="isAddNewPlatformGroup">
                      <b-td class="">
                        <validation-provider #default="{ errors }" name="Group Name" rules="required" >
                          <b-form-input v-model="platform_group_item.name" :state="errors.length > 0 ? false:null" />
                          <small class="text-danger">{{ errors[0] }}</small>
                        </validation-provider>
                      </b-td>
                      <b-td class="">
                        <!-- Select parent group and Calculate path -->
                        <validation-provider #default="{ errors }" name="Parent Group" rules="required" >
                          <v-select @input="chooseTeams" v-model="form_select.platform_group_parent" :options="calcTeamsOptions" label="text"/>
                          <small class="text-danger">{{ errors[0] }}</small>
                        </validation-provider>
                      </b-td>  
                      <b-td class="">
                        <validation-provider #default="{ errors }" name="Server Name" rules="required" >
                          <v-select v-model="platform_group_item.server_name" :options="Object.keys(server_name_options)"/>
                          <small class="text-danger">{{ errors[0] }}</small>
                        </validation-provider>
                      </b-td>
                      <b-td class="">
                        <validation-provider #default="{ errors }" name="Platform Group" rules="required" >
                          <v-select v-model="platform_group_item.platform_group" :options="server_name_options[platform_group_item.server_name] || []" label="group_name" :reduce="g => g.platform_group"/>
                          <small class="text-danger">{{ errors[0] }}</small>
                        </validation-provider>
                      </b-td>
                      <b-td class="">
                        <b-form-input :value="calc_currency" disabled/>
                      </b-td>
                      <b-td class="">
                        {{ (new Date()).toLocaleDateString("en-US") }}
                      </b-td>
                    </b-tr>
                  </b-tbody>


                </b-table-simple>
              </validation-observer>

              <div class="p-1">
                <b-button variant="gradient-success" size="sm" pill @click="isAddNewPlatformGroup = true" v-if="!isAddNewPlatformGroup">+ New Group</b-button>
                <span v-else>
                  <b-button variant="gradient-info" size="sm" pill @click="isAddNewPlatformGroup = false" >Cancel</b-button>
                  <b-button variant="gradient-success" class="ml-1" size="sm" pill @click="savePlatformGroup()">Save</b-button>
                </span>
              </div>

            </div>
          </div>

          <!-- Group Levels -->
          <div class="mb-5">
            <h4 class="mb-3">Group Levels</h4>
            <div class="d-flex">
              <div class="text-center mr-4" v-for="(item, index) in group_levels" :key="index">
                <h5 class="mb-2">Level {{index+1}}</h5>
                <div class="d-flex"> 
                  <b-form-input
                    :disabled="!is_edit_group_level"
                    class="my-shadow group-levels-input" 
                    id="basicInput" v-model="group_levels[index]"
                  />
                  <feather-icon icon="XIcon" size="18" class="text-danger cursor-pointer ml-1 my-auto" v-if="is_edit_group_level" @click="group_levels.splice(index, 1)"/>
                </div>
                
              </div>
              <feather-icon v-if="!is_edit_group_level" icon="EditIcon" size="18" class="text-success cursor-pointer mt-4" @click="is_edit_group_level=true"/>
              <span v-else>
                <feather-icon icon="PlusIcon" size="18" class="text-success cursor-pointer mt-4 mr-1" @click="group_levels.push(`Level ${group_levels.length + 1}`)"/>
                <feather-icon icon="SaveIcon" size="18" class="text-info cursor-pointer mt-4 mr-1" @click="save_group_levels()"/>
                <feather-icon icon="LogOutIcon" size="18" class="text-warning cursor-pointer mt-4" @click="is_edit_group_level = false"/>
              </span>
            </div>
          </div>

          <!-- Group Organization -->
          <div>
            <h4 class="mb-1">Group Organization</h4>
            <div class="rounded my-shadow px-3 py-2 w-50">
              <v-jstree :data="tree_view"  allow-batch whole-row @item-click="()=>{}"></v-jstree>
              <div class="p-1">
                <b-button variant="gradient-success" size="sm" pill  v-b-modal.modal-lg>+ New Group Folder</b-button>
              </div>
            </div>
          </div>

          <!-- Modal -->
          <b-modal id="modal-lg" hide-footer no-close-on-backdrop centered size="lg" title="New Group Folder"  ref="my-modal">
            <validation-observer ref="modalNewGroupFolder">
              <div class="d-flex"> 
                <div class="mr-2">
                  Folder Name
                  <validation-provider #default="{ errors }" name="Folder Name" rules="required">
                    <b-form-input class="mt-2 width-170" v-model="group_folder_item.name" :state="errors.length > 0 ? false:null"/>
                    <small class="text-danger">{{ errors[0] }}</small>
                  </validation-provider>
                </div>

                <div class="mr-2">
                  Level
                  <validation-provider #default="{ errors }" name="Level" rules="required">
                    <v-select label="Level" :options="group_levels" class="mt-2 width-170" v-model="group_folder_item.level"/>
                    <small class="text-danger">{{ errors[0] }}</small>
                  </validation-provider>
                </div>

                <div class="mr-2" v-for="(item, index) in group_levels" :key="index" v-show="index < group_levels.indexOf(group_folder_item.level)">
                  {{item}}
                  <v-select label="Level" class="mt-2 width-170" :options="calcLevelOpions(index)" v-model="filter[index]"/>
                </div>
              </div>
            </validation-observer>
            <b-button class="mt-3" @click="create_new_group_folder()">Save</b-button>
          </b-modal>
          
        </div>
      </b-tab>
      
      <b-tab @click="$router.push('/settings/risk-parameters')">
        <template #title>
          <div style="width: 168px"><feather-icon icon="SlidersIcon" /> Risk Parameters</div>
        </template>
      </b-tab>

    </b-tabs>
  </div>
</template>

<script>
import { BTabs, BTab, BFormInput, BModal, VBModal } from "bootstrap-vue";
import { BTable, BProgress, BBadge, BTableSimple, BThead, BTr, BTh, BTd, BTbody, BTfoot, BButton } from 'bootstrap-vue'
import ToastificationContent from '@core/components/toastification/ToastificationContent.vue'
import vSelect from "vue-select";
import VJstree from 'vue-jstree'
import axios from '@axios'
import VueSelect from '../forms/form-element/vue-select/VueSelect.vue';

// validation
import { ValidationProvider, ValidationObserver } from 'vee-validate'
import { required} from '@validations'

export default {
  components: {
    BTabs, BTab, BFormInput, ValidationProvider, ValidationObserver,
    BTable, BProgress, BBadge, BTableSimple, BThead, BTr, BTh, BTd, BTbody, BTfoot, BButton,
    vSelect, VJstree,
    ToastificationContent,
    BModal,
    VueSelect
  },

  directives: {
    'b-modal': VBModal,
  },
  data() {
    return {
      required,
      is_edit_group_level: false,
      group_levels: [],
    
      group_organization: {},
      group_folder_item: {
        name: null,
        level: null
      },
      // filter for get exactly group level
      filter: [],
      site_settings_path: 'site_settings/risk_management/group_organization/',

      isAddNewPlatformGroup: false,
      platform_groups: [],

      teams: [],
      // add platform Group
      server_name_options: {},
      platform_group_item: {
        "name": "",
        "path": "",
        "created": "",
        "currency": "",
        "server_name": "",
        "platform_group": ""
      },

      form_select: {
        platform_group_parent: ''
      }

    }
  },

  computed: {
    calc_currency(){
      let groups = this.server_name_options[this.platform_group_item.server_name]
      
      let selected_group = this.platform_group_item.platform_group
      if(groups && groups.length && selected_group) {
        return groups.filter(x => x.platform_group == selected_group)[0].currency
      } 
      return ''
    },

    tree_view(){
      if(this.group_organization.children) {
        return this.getTree(this.group_organization)
      }
      return []
    },

    calcTeamsOptions(){
      
      if(this.teams.length){
        return this.teams.map( item => {
          return {
            text: Object.values(item)[0].settings.name,
            value: item.path + '/' + Object.keys(item)[0]
          }
        })
      }
      return []
    }
  },


  mounted() {
    axios.get(`${process.env.VUE_APP_API_URL}site_settings/risk_management/group_levels`).then(res => {
      this.group_levels = res.data.settings
    })

    this.fetch_groups()
    this.fetch_trading_server()

    this.$store.commit('app/SET_DATA_LIVE_TIME', '')
  },

  methods: {
    fetch_trading_server(){
      axios.get(`${process.env.VUE_APP_API_URL}public_site_settings/trading_platforms?recursive`).then(res => {
        Object.keys(res.data.children).map( key => {
          if(key != 'default_platform' && res.data.children[key].settings.platform_state == 'running')
            this.server_name_options[key] = res.data.children[key].children.groups.settings
        })
      })
      console.log('this.server_name_options', this.server_name_options)
    },

    savePlatformGroup(){
      this.$refs.platformGroup.validate().then(success => {
        if (success) {
          let setting = {...this.platform_group_item, level: this.group_levels.length + 1,  currency: this.calc_currency, created: (new Date()).toLocaleDateString("en-US") }

          axios.post(`${process.env.VUE_APP_API_URL}site_settings/risk_management${setting.path}`, {setting, risk_management: 1}).then(res =>{
            this.isAddNewPlatformGroup = false
            this.platform_group_item = {
              "name": "",
              "path": "",
              "created": "",
              "currency": "",
              "server_name": "",
              "platform_group": ""
            },
            this.form_select.platform_group_parent = ''
            this.fetch_groups()
            this.$toast({
              component: ToastificationContent,
              props: {
                title: 'Successfully',
                text: 'Save successfully!',
              },
            })
          }).catch(err => alert('error', err))
        }
      })

      


    },

    chooseTeams(e){
      this.platform_group_item.path = e ? e.value : ''
    },
    
    getChildren(sitesettings, depth, current_path="") {
      
      let x = current_path + '/' + sitesettings.short_name
      if (depth == 0) {
        if(sitesettings["children"]) return {...sitesettings["children"], parent_group: sitesettings.settings, path: x};
        return 0
      }
      let result = [];
      if (sitesettings["children"]) {
        Object.values(sitesettings["children"]).forEach((val) => {
          let y = this.getChildren(val, depth - 1, x)
          if(y) result = result.concat(y);
        })
      }
      return result;
    },


    getTree(sitesettings) {
      let tree = []
      if (sitesettings["children"]) {
        Object.values(sitesettings["children"]).forEach((val) => {
          let child = {
            "text": val["settings"]["name"],
            "opened": true,
            "children": this.getTree(val)
          }
          tree.push(child);
        })
      }
      return tree
    },

    fetch_groups(){
      axios.get(`${process.env.VUE_APP_API_URL}site_settings/risk_management/group_organization?recursive`).then(res => {
        this.group_organization = res.data
        
        let platform_groups = []
        this.getChildren(this.group_organization, 3).map(g => {
          Object.keys(g).filter(k => k != 'path' && k != 'parent_group').map(gg => {
            platform_groups.push({...g[gg], parent_group: g.parent_group.name})
          })
        })
        this.platform_groups = platform_groups

        console.log('platform_groups', this.platform_groups)

        this.teams = this.getChildren(this.group_organization, 2)
        console.log('teams', this.teams)
      })
    },

    save_group_levels(){ 
      axios.patch(`${process.env.VUE_APP_API_URL}site_settings/risk_management/group_levels`, {setting: this.group_levels}).then(res => {
        this.$toast({
          component: ToastificationContent,
          props: {
            title: 'Successfully',
            text: 'Save successfully!',
          },
        })
        this.is_edit_group_level = false
      })
    },

    create_new_group_folder() {
       this.$refs.modalNewGroupFolder.validate().then(success => { 
        if (success) {

          let setting  = {...this.group_folder_item, level: this.group_levels.indexOf(this.group_folder_item.level) + 1}

          let path = this.site_settings_path
          if(setting.level > 1) path += this.filter.slice(0, setting.level - 1).join('/')

          axios.post(`${process.env.VUE_APP_API_URL}${path}`, {setting, risk_management: 1}).then(res =>{
            this.$refs['my-modal'].hide()
            this.fetch_groups()
            this.$toast({
              component: ToastificationContent,
              props: {
                title: 'Successfully',
                text: 'Save successfully!',
              },
            })
          }).catch(err => alert('error', err))
        }
      })
    },
    
    compare2string(str1, str2){ //HUST-xyz HUST xyz
      return str1 && str2 && str1.split(' ').join('-').toLocaleLowerCase() == str2.split(' ').join('-').toLocaleLowerCase()
    },

    calcLevelOpions(index){
      if(index == 0 && this.group_organization.children)
        return Object.keys(this.group_organization.children)
      
      if(index > 0) {
        // Check parent filter
        let checked = true
        for(let i = 0; i < index; i++)
          checked &&= this.filter[i]

        if(checked){
          let x = this.getChildren(this.group_organization, index).filter(item => this.compare2string(item.parent_group.name, this.filter[index - 1]))[0]
          return Object.keys(x).filter(item => item != 'parent_group' && item != 'path')
        }
        
      }

        
    }
  },
}
</script>
<style>
  .setting-tab th {
    height: 70px;
    padding-top: 24px;
  }

  .setting-tab a {
    background: #242526!important;
  }

  .setting-tab .b-table-sticky-header {
    height: 50vh;
    max-height: 50vh!important;
  }

  .group-levels-input{
    border-radius: 20px; 
    width: 120px
  }
  .width-170 {
    width: 170px
  }
</style>